<template>
<div class="d1">
  <div class="left col-sm-12">
    <criteria-paged :model="model" v-ref:cp>
        <criteria partial='criteria' @condition-changed='search'  v-ref:cri>
          <form novalidate class="" partial >
          </form>
      </criteria>
      <data-grid :model="model" partial='list' v-ref:grid class="data-grid" is-fixed='false' style="overflow-x:auto">
        <template partial='head'>
          <tr class="title">
            <!-- <th><nobr>姓名</nobr></th> -->
            <th><nobr>报建业务类型</nobr></th>
            <th><nobr>描述</nobr></th>
            <th><nobr>状态</nobr></th>
          </tr>
        </template>
        <template partial='body'>
          <tr>
            <td><nobr>{{row.f_operat_type}}</nobr></td>
            <td><nobr>{{row.f_describe}}</nobr> </td>
            <td><nobr>{{row.f_state}}</nobr></td>
            <!-- <td style="text-align: center;">
              <nobr>
                <a class="" @click="$parent.$parent.$parent.clifun(row)">
                  查看
                </a>
              </nobr>
            </td> -->
          </tr>
        </template>
      </data-grid>
    </criteria-paged>
  </div>
</div>
</template>
<script>
  // import AppData from '../../../../stores/AppData.js'
  import {PagedList} from 'vue-client'

  export default {
  title: '报建详情信息',
  data () {
    return {
      model: new PagedList('rs/sql/installonerecord', 50, {data: {id: `${this.applyinfo.id}`}})
    }
  },
  props: ['applyinfo'],
  ready () {
    this.$refs.cp.$refs.cri.search()
    this.$refs.cp.pager = false
  },
  methods: {
    clifun (row) {
      this.$dispatch('showline', row)
    }
  }
}
</script>



